@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&family=Montserrat:ital,wght@1,700&family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: "Titillium Web", sans-serif;
}
nav{
  padding: 15px !important;
}
a{
  font-size: 1rem;
  margin-left : 15px;
  margin-right: 15px;
  color : white;
  font-weight: 500;
  text-decoration: none;
}
.brand{
  font-size: 1.5rem;
}
a:hover{
  text-decoration: none;
  color: white;
  font-weight: 500;
}
.logo, .logo:hover{
  font-weight: bold !important;
  letter-spacing: 2px !important;
}

@media only screen and (max-width: 800px) {
  .hero h1{
    font-size: 3.2rem !important;
  }
  .footer{
    display: block !important;
  }
  .footer .footer-info{
      width: 95% !important;
      margin-bottom: 5px !important;
  }    
}
.hero {
	min-height: 500px;
	background-image: url(/images/banner-pizza.jpg);
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	font-family: 'Montserrat', sans-serif !important;
	/*font-family: 'Poppins', sans-serif;*/
	color: #fff;
}
.show-button:hover{
	cursor: pointer;
}
.hero .text-block {
	padding: 80px 0;
}
.hero p {
	text-transform: uppercase;
	color: #444;
	font-family: 'Poppins', sans-serif;
	letter-spacing: 2px;
	font-size: 2.5rem;
}

.hero h1 {
	font-family: 'Montserrat', sans-serif !important;
	padding: 0px !important;
	font-size: 4.5rem;
	color: #444;
	text-shadow: 2px 2px 5px #ddd;
    text-transform: uppercase;
}

.hero h2 {
	font-family: 'Poppins', sans-serif;
	font-size: 1.2rem;
	letter-spacing: 2px;
	font-weight: 300;
}

.hero .button {
	padding: 15px 20px;
	background: var(--mainColor);
	color: var(--textColor);
	cursor: pointer;
	margin-top: 30px;
	border-radius: 20px;
	border: none;
	font-size: 1.2rem;
    background-color: #6a994e;
}

.hero .button:hover{
    background-color: #386641;
}

.hero button:hover {
	background: var(--textColor);
	color: var(--mainColor);
}
.home-dish-promotion{
    padding: 40px 0;
}
.home-dish-promotion .row{
    margin-bottom: 30px;
}
.home-dish-promotion .product-image{
    max-width: 100%;
    transition: all .3s ease-in-out;
}
.home-dish-promotion .product-image:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
} 
.home-dish-promotion .egg-image-tag{
    position: absolute;
    right: 10%;
    max-width: 100%;
    z-index: 1;
}
.home-dish-promotion .salmon-image-tag{
    position: absolute;
    max-width: 100%;
    z-index: 1;
}
.home-dish-promotion h1{
    font-family: 'Montserrat', sans-serif !important;
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 20px;
}
.home-dish-promotion .bold{
    font-weight: bold;
}
.home-dish-promotion .green{
    color: var(--secondaryColor);
}
.home-dish-promotion .red{
    color: #da2424;
}
.home-dish-promotion .price{
    font-size: 1.5rem;
}
.home-dish-promotion .btn{
    margin-right: 10px;
    padding: 15px 30px;
    border-radius: 20px;
    border: none;
}
.home-dish-promotion .btn-primary{
    background-color: var(--secondaryColor);
}
.home-dish-promotion .btn-primary:hover{
    background-color: #da2424;
    color: #fff;
}
.home-dish-promotion .btn-danger:hover{
    background-color: #da2421;
    color: #fff;
}
a:hover{
    text-decoration: none;
}
#about {
   background: url("/images/about-bg.jpg") center top no-repeat fixed;
   background-size: cover;
   padding: 29px 0 40px 0;
   position: relative;
}
#about .product-image{
   max-width: 100%;
}
#about .title a{
   font-size: 1.8rem !important;
    margin: 20px 0;
    display: block;
    font-weight: bold !important;
}
.section-header h3 {
   font-size: 32px;
   text-transform: uppercase;
   text-align: center;
   font-weight: 700;
   position: relative;
   padding-bottom: 15px;
}

.section-header h3::before {
   content: '';
   position: absolute;
   display: block;
   width: 120px;
   height: 1px;
   background: #ddd;
   bottom: 1px;
   left: calc(50% - 60px);
}

.section-header h3::after {
   content: '';
   position: absolute;
   display: block;
   width: 40px;
   height: 3px;
   background: #18d26e;
   bottom: 0;
   left: calc(50% - 20px);
}

.section-header p {
   text-align: center;
   padding-bottom: 30px;
}
#about .price{
   text-align: center !important;
   font-size: 1.3rem !important;
   font-weight: bold;
   color: crimson !important;
}
#about .container {
   position: relative;
   background: rgba(0, 0, 0, 0.7);
   padding: 30px;
   border-radius: 10px;
   color: #fff;
}

#about .about-col {
   background: #fff;
   border-radius: 0 0 4px 4px;
   box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.08);
   margin-bottom: 20px;
   padding:20px;
}

#about .about-col .img {
   position: relative;
}

#about .about-col .img img {
   border-radius: 4px 4px 0 0;
}

#about .about-col .icon {
   width: 64px;
   height: 64px;
   padding-top: 8px;
   text-align: center;
   position: absolute;
   background-color: #18d26e;
   border-radius: 50%;
   text-align: center;
   border: 4px solid #fff;
   left: calc( 50% - 32px);
   bottom: -30px;
   transition: 0.3s;
}

#about .about-col i {
   font-size: 24px;
   line-height: 1.5;
   color: #fff;
   transition: 0.3s;
}

#about .about-col:hover .icon {
   background-color: #fff;
}

#about .about-col:hover i {
   color: #18d26e;
}

#about .about-col h2 {
   color: #000;
   text-align: center;
   font-weight: 700;
   font-size: 20px;
   padding: 0;
   /*margin: 40px 0 12px 0;*/
}

#about .about-col h2 a {
   color: #000;
}

#about .about-col h2 a:hover { 
   color: #18d26e;
}

#about .about-col p {
   font-size: 14px;
   line-height: 24px;
   color: #333;
   margin-bottom: 0;
   padding: 0 20px 20px 20px;
   text-align: justify;
}

#about .about-col h2 a{
   text-decoration: none;
}
#about .btn {
   margin-right: 10px;
   padding: 15px 30px;
   border-radius: 20px;
   border: none;
}
