div .wrapper-contact {
    font-family: "Titillium Web", sans-serif !important;
    background-color: rgba(96, 168, 69, 0.2);
  }
  
  .wrapper-contact input {
    width: auto;
  }
  
  div .contact-form {
    background-color: rgba(96, 168, 69, 0.1);
  }
  
  .contact-wrapper {
    margin: auto 0;
  }
  
  .form-headline:after {
    content: "";
    display: block;
    width: 10%;
    padding-top: 10px;
    border-bottom: 3px solid #ec1c24;
  }
  
  .highlight-text {
    color: #ec1c24;
  }
  
  .hightlight-contact-info {
    font-weight: 700;
    font-size: 22px;
    line-height: 1.5;
  }
  
  .highlight-text-grey {
    font-weight: 500;
  }
  
  .email-info {
    margin-top: 20px;
  }
  
  ::-webkit-input-placeholder {
    /* Chrome */
    font-family: 'Roboto', sans-serif;
  }
  
  .required-input {
    color: black;
  }
  
  @media (min-width: 600px) {
    .contain {
      padding: 0;
    }
  }
  
  h3,
  ul {
    margin: 0;
  }
  
  h3 {
    margin-bottom: 1rem;
  }
  
  .form-input:focus,
  textarea:focus {
    outline: 1.5px solid #ec1c24;
  }
  
  .form-input,
  textarea {
    width: 100%;
    border: 1px solid #bdbdbd;
    border-radius: 5px;
  }
  
  .wrapper-contact>* {
    padding: 1em;
  }
  
  @media (min-width: 700px) {
    .wrapper-contact {
      display: grid;
      grid-template-columns: 2fr 1fr;
    }
  
    .wrapper-contact>* {
      padding: 2em 2em;
    }
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  .contacts {
    color: #212d31;
  }
  
  .form {
    background: #fff;
  }
  
  #submit-form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
  }
  
  #submit-form label {
    display: block;
  }
  
  #submit-form p {
    margin: 0;
  }
  
  .full-width {
    grid-column: 1 / 3;
  }
  
  button,
  .submit-btn,
  .form-input,
  textarea {
    padding: 1em;
  }
  
  button,
  .submit-btn {
    background: transparent;
    border: 1px solid #ec1c24;
    /* color: #ec1c24; */
    border-radius: 15px;
    padding: 5px 20px;
    text-transform: uppercase;
  }
  
  button:hover,
  .submit-btn:hover,
  button:focus,
  .submit-btn:focus {
    /* background: #ec1c24; */
    outline: 0;
    color: #eee;
  }
  
  .error {
    color: #ec1c24;
  }
